//  0- 1- 2-
class TestingFactory {
    // 
    getOrignData () {
        return {
            description: void 0, // 
            score: void 0, // 
            image: void 0, // 
            explain: void 0, // 
            correct: void 0,
            editable: true,
            options: [{ // 
                sort: 1,
                description: '',
                correct: void 0 //  1
            }]
        }
    }

    /**
     * 
     * @param type 0- 1- 2-
     */
    getTestingSet (type) {
        let data = this.getOrignData()
        data.category = type
        if (type == 0) {
            delete data.options
            data.correct = void 0
            return data
        } else if (type == 1 || type == 2) {
            return data
        }
    }
}

export default new TestingFactory()



